<template><section><html><head></head><body><h1>Billboard</h1>
<p><code v-pre="">vc-primitive-billboard</code> 组件用于加载布告板图元，布告板是场景中与视口对齐的图像，只能作为 <code v-pre="">vc-collection-primitive-billboard</code> 的子组件使用。</p>
<h2>示例</h2>
<h3>加载布告板图元</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-primitive-billboard>
          <vc-primitive-billboard :image="image" :scale="0.4" :show="show" :distance-display-condition="distanceDisplayCondition" :horizontal-origin="horizontalOrigin" :position="position" @click="clicked"></vc-primitive-billboard>
        </vc-collection-primitive-billboard>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-collection-primitive-billboard</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive-billboard</span>
          <span class="hljs-attr">:image</span>=<span class="hljs-string">"image"</span>
          <span class="hljs-attr">:scale</span>=<span class="hljs-string">"0.4"</span>
          <span class="hljs-attr">:show</span>=<span class="hljs-string">"show"</span>
          <span class="hljs-attr">:distance-display-condition</span>=<span class="hljs-string">"distanceDisplayCondition"</span>
          <span class="hljs-attr">:horizontal-origin</span>=<span class="hljs-string">"horizontalOrigin"</span>
          <span class="hljs-attr">:position</span>=<span class="hljs-string">"position"</span>
        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive-billboard</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-collection-primitive-billboard</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">id</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,
        <span class="hljs-attr">image</span>: <span class="hljs-string">'https://zouyaoji.top/vue-cesium/favicon.png'</span>,
        <span class="hljs-attr">position</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">108</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">35</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">10000</span> },
        <span class="hljs-attr">billboard</span>: {},
        <span class="hljs-attr">show</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">distanceDisplayCondition</span>: { <span class="hljs-attr">near</span>: <span class="hljs-number">0</span>, <span class="hljs-attr">far</span>: <span class="hljs-number">20000000</span> },
        <span class="hljs-attr">horizontalOrigin</span>: <span class="hljs-number">0</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = cesiumInstance
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>alignedAxis</td>
<td>Cartesian3</td>
<td><code v-pre="">{x: 0, y: 0, z: 0}</code></td>
<td><code v-pre="">optional</code> 指定 billboard 按单位矢量轴旋转参数。<strong>结构：{ x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre="">'white'</code></td>
<td><code v-pre="">optional</code> 指定 billboard 图片的颜色。</td>
</tr>
<tr>
<td>disableDepthTestDistance</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 的深度检测距离。</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 的显示条件。. <strong>结构: { near: number, far: number }</strong></td>
</tr>
<tr>
<td>eyeOffset</td>
<td>Object</td>
<td><code v-pre="">{x: 0, y: 0, z: 0}</code></td>
<td><code v-pre="">optional</code> 指定 billboard 视角偏移量。 <strong>结构：{ x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 的高度（像素）。</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 billboard 高度模式。<strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>horizontalOrigin</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 billboard 水平对齐方式。<strong>CENTER: 0, LEFT: 1, RIGHT: -1</strong></td>
</tr>
<tr>
<td>id</td>
<td>*</td>
<td></td>
<td><code v-pre="">optional</code> 指定与 billboard 关联的信息。</td>
</tr>
<tr>
<td>image</td>
<td>String|Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 加载的的 Image、 URI 或者 Canvas。</td>
</tr>
<tr>
<td>pixelOffset</td>
<td>Object</td>
<td><code v-pre="">{x: 0, y: 0}</code></td>
<td><code v-pre="">optional</code> 指定 billboard 像素偏移量。 <strong>结构：{ x: number, y: number }</strong></td>
</tr>
<tr>
<td>pixelOffsetScaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 偏移量随相机距离改变的参数。<strong>结构：{ near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 的位置。 <strong>结构：{ lng: number, lat: number, height: number }</strong></td>
</tr>
<tr>
<td>rotation</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 billboard 沿 x 轴方向旋转的角度。</td>
</tr>
<tr>
<td>scale</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> 指定 billboard 缩放比例。</td>
</tr>
<tr>
<td>scaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 的缩放显示参数。 <strong>结构： { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>指定 billboard 是否显示。</td>
</tr>
<tr>
<td>sizeInMeters</td>
<td>Boolean</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 的单位是否是米。</td>
</tr>
<tr>
<td>translucencyByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 billboard 透明度改变参数。 <strong>结构： { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>verticalOrigin</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 billboard 垂直对齐方式。<strong>CENTER: 0, BOTTOM: 1, BASELINE: 2, TOP: -1</strong></td>
</tr>
</tbody>
</table>
<hr>
<p>参考官方文档: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/Billboard.html">Billboard</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该布告板上按下时触发。</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该布告板上弹起时触发。</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标单击该布告板时触发。</td>
</tr>
<tr>
<td>clickout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标单击该布告板外部时触。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标左键双击该布告板时触发。</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该布告板上移动时触发。</td>
</tr>
<tr>
<td>mouseover</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移动到该布告板时触发。</td>
</tr>
<tr>
<td>mouseout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移出该布告板时触发。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          id: 'Hello Vue Cesium',
          image: 'https://zouyaoji.top/vue-cesium/favicon.png',
          position: { lng: 108, lat: 35, height: 10000 },
          billboard: {},
          show: true,
          distanceDisplayCondition: { near: 0, far: 20000000 },
          horizontalOrigin: 0
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
        },
        clicked (e) {
          console.log(e)
        }
      }
    }
  </script>